import React from 'react'
import {motion} from 'framer-motion'

function Design() {
    return (
        <motion.div 
        initial={{ opacity: 0, y: 100 }}
        animate={{ opacity: 1, y: 20 }}
        transition={{ ease: "easeOut", duration: 0.6}} 
        className="flex flex-col justify-center items-center w-9/12 pb-64 h-full">
            <p className="text-lg w-full text-center mb-8">
               Good Design is key to every product success. When a product is build with both good Design Principles and Technical Stack, 
                it always lead to success. I'm experienced to work in top softwares in design industries like Figma, Adobe XD, Adobe Illustrator, 
                Adobe Photoshop, Adobe After Effects and Adobe Premiere Pro.
            </p>
        <div class="px-5 py-8 mx-auto flex flex-wrap z-0 items-center justify-center flex-col">
            <div className="flex flex-row justify-start items-start">
                <div className="flex relative pb-20 sm:items-center md:w-2/3 mx-auto">
                    <div className="flex-grow md:pl-8 pl-6 flex flex-col justify-center items-center self-center">
                        <div className="flex-shrink-0 mb-5 w-24 ml-6 h-24 bg-yellow-200 text-yellow-500 rounded-full inline-flex items-center justify-center">
                            <svg fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" class="w-12 h-12" viewBox="0 0 24 24">
                                <path d="M12 22s8-4 8-10V5l-8-3-8 3v7c0 6 8 10 8 10z"></path>
                            </svg>
                        </div>
                        <div className="flex-grow sm:pl-6 mt-6 sm:mt-0 flex flex-col items-center text-center">
                            <h2 className="font-bold title-font text-gray-900 mb-1 text-xl">UI/UX</h2>
                            <p className="leading-relaxed">Specialized in  Design Principles, Figma and Adobe XD</p>
                        </div>
                    </div>
                </div>
                <div className="flex relative pb-20 sm:items-center md:w-2/3 mx-auto">
                    <div className="flex-grow md:pl-8 pl-6 flex items-center flex-col">
                        <div className="flex-shrink-0 mb-5 w-24 h-24 bg-yellow-200 text-yellow-500 rounded-full inline-flex items-center justify-center">
                            <svg fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" className="w-12 h-12" viewBox="0 0 24 24">
                                <path d="M22 12h-4l-3 9L9 3l-3 9H2"></path>
                            </svg>
                        </div>
                        <div className="flex-grow sm:pl-6 mt-6 sm:mt-0 text-center">
                            <h2 className="font-bold title-font text-gray-900 mb-1 text-xl">Graphic Design</h2>
                            <p className="leading-relaxed">Expertized in Illustrator, InDesign, Photoshop</p>
                        </div>
                    </div>
                </div>
                <div className="flex relative pb-20 sm:items-center justify-center md:w-2/3 mx-auto">
                <div className="flex-grow md:pl-8 pl-6 flex items-center flex-col">
                    <div className="flex-shrink-0 mb-5 w-24 ml-4 h-24 bg-yellow-200 text-yellow-500 rounded-full inline-flex items-center justify-center">
                    <svg fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" className="w-12 h-12" viewBox="0 0 24 24">
                        <circle cx="12" cy="5" r="3"></circle>
                        <path d="M12 22V8M5 12H2a10 10 0 0020 0h-3"></path>
                    </svg>
                    </div>
                    <div className="flex-grow sm:pl-6 mt-6 sm:mt-0 text-center">
                    <h2 className="font-bold title-font text-gray-900 mb-1 text-xl">MoGraphs</h2>
                    <p className="leading-relaxed">Intermediate Knowledge in After Effects, Premiere Pro with Motion Principles</p>
                    </div>
                </div>
                </div>
            </div>
            </div>
            <div className="grid grid-row-2 w-full gap-6 h-64">
                <div className="grid grid-cols-4 h-56 gap-6 row-span-1">
                    <div className="col-span-1 bg-yellow-200">

                    </div>
                    <div className="col-span-2 bg-yellow-200">

                    </div>
                    <div className="col-span-1 bg-yellow-200">

                    </div>
                </div>
                <div className="grid grid-cols-4 h-56 gap-6 row-span-1">
                    <div className="col-span-1 bg-yellow-200">

                    </div>
                    <div className="col-span-1 bg-yellow-200">

                    </div>
                    <div className="col-span-2 bg-yellow-200">

                    </div>
                </div>
            </div>
        </motion.div>
    )
}

export default Design